import React, {Component} from 'react';
import {Grid, Cell} from 'react-mdl';

class Skills extends Component {
    render(){
        return(
            <Grid>
                <Cell col={12}>
                    <div style={{display: 'flex'}}>
                        <ul>
                            <div style={{fontSize: "25px"}}>
                                {this.props.skill} 
                            </div> 
                            <li>
                                {this.props.list.map(item => 
                                    <li> {item} </li>)}
                            </li>
                        </ul>
                    </div>
                </Cell>
            </Grid>
        )
    }
}

export default Skills;